// draggable.js
export default {
    mounted(el) {
      let isDragging = false;
      let startX = 0, startY = 0;
      let initialTransform = el.style.transform; //  transform
      let initialCursor = document.body.style.cursor; // 
  
      //  fixed 
      const computedStyle = window.getComputedStyle(el);
      if (computedStyle.position !== 'fixed') {
        el.style.position = 'fixed';
      }
  
      //  transform  left/top
      const rect = el.getBoundingClientRect();
      el.style.transform = 'none'; //  transform
      el.style.left = `${rect.left}px`;
      el.style.top = `${rect.top}px`;
  
      const onMouseMove = (e) => {
        if (!isDragging) return;
        el.style.left = `${e.clientX - startX}px`;
        el.style.top = `${e.clientY - startY}px`;
      };
  
      const onMouseUp = () => {
        isDragging = false;
        document.body.style.cursor = initialCursor; // 
        document.removeEventListener('mousemove', onMouseMove);
        document.removeEventListener('mouseup', onMouseUp);
      };
  
      el.onmousedown = (e) => {
        isDragging = true;
        document.body.style.cursor = 'grabbing'; // 
  
        // 
        const left = parseFloat(el.style.left) || 0;
        const top = parseFloat(el.style.top) || 0;
        startX = e.clientX - left;
        startY = e.clientY - top;
  
        e.preventDefault(); // 
        document.addEventListener('mousemove', onMouseMove);
        document.addEventListener('mouseup', onMouseUp);
      };
  
      // 
      el.cleanup = () => {
        document.body.style.cursor = initialCursor;
        document.removeEventListener('mousemove', onMouseMove);
        document.removeEventListener('mouseup', onMouseUp);
        el.style.transform = initialTransform; //  transform
      };
    },
  
    unmounted(el) {
      if (el.cleanup) el.cleanup();
    }
  };